.review {
  &__header {
    height: 80px;
    margin-bottom: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid #808080;
    display: flex;
    justify-content: space-between;
    align-items: center;

    span {
      font-size: 22px;
    }

    button {
      width: 240px;
      height: 40px;
      font-size: 18px;
      font-weight: 700;
      color: #ffffff;
      background-color: #ffa500;
      border: none;
      border-radius: 15px;
      cursor: pointer;
      transition: background-color 0.2s ease;

      &:hover {
        background-color: #a52a2a;
      }
    }
  }

  &__form {
    width: 100%;
    max-height: 0px;
    opacity: 0;
    visibility: hidden;
    transition: max-height 0.5s ease, opacity 0.5s ease;

    .rating {
      display: flex;
      margin-bottom: 10px;
      align-items: center;
      p {
        margin: 0;
        margin-right: 20px;
      }
      span {
        margin-top: -5px;
      }
    }

    &-text {
      width: 100%;
      height: 300px;

      textarea {
        width: 100%;
        height: 100%;
        padding: 10px;
        resize: none;
        overflow-y: auto;
        font-size: 18px;
        line-height: 100%;
      }
      textarea::-webkit-scrollbar-track {
        border-radius: 15px;
      }
      
      textarea::-webkit-scrollbar {
        width: 7px;
      }
      
      textarea::-webkit-scrollbar-thumb {
        background: #ffa500;
        border-radius: 15px;
      }
    }

    &-controls {
      width: 100%;
      padding-top: 20px;
      display: flex;
      justify-content: space-between;

      &-file {
        display: flex;
        align-items: center;

        label {
          width: 240px;
          height: 40px;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 18px;
          line-height: 40px;
          color: #43a948;
          background-color: #ffffff;
          border: 2px solid #43a948;
          border-radius: 15px;
          cursor: pointer;
          transition: background-color 0.2s ease, color 0.2s ease;

          &:hover {
            background-color: #43a948;
            color: #ffffff;
          }
        }

        input {
          display: none;
        }

        button {
          width: 40px;
          height: 40px;
          margin-left: 10px;
          display: flex;
          align-items: center;
          justify-content: center;
          background-color: transparent;
          border: 1px solid #ffffff;
          border-radius: 50%;
          cursor: pointer;
          transition: border 0.2s ease;

          &:hover {
            border: 1px solid #ffa500;
          }
        }

        svg {
          font-size: 30px;
        }
      }

      &-buttons {
        button {
          width: 240px;
          height: 40px;
          margin-left: 10px;
          font-size: 18px;
          color: #ffffff;
          background-color: #ffa500;
          border: none;
          border-radius: 15px;
          cursor: pointer;
          transition: background-color 0.2s ease;

          &:hover {
            background-color: #a52a2a;
          }
        }
      }
    }

    &-photo {
      max-width: 100%;
      height: 300px;
      padding-top: 20px;

      img {
        max-width: 100%;
        max-height: 100%;
      }
    }

    .form-blocked {
      font-size: 16px;
      text-align: center;
      margin-bottom: 35px;
      margin: 0 10px;

      & a {
        font-weight: 700;
        text-decoration: none;

        &:hover {
          text-decoration: underline;
        }
      }
    }
  }

  .show {
    max-height: 1000px;
    margin-bottom: 20px;
    padding: 0px 20px 20px 20px;
    border-bottom: 1px solid #808080;
    opacity: 1;
    visibility: visible;
  }

  &__item {
    margin-bottom: 20px;
    padding: 0px 20px 20px 20px;
    border-bottom: 1px solid #808080;
    display: flex;

    &-avatar {
      height: 100%;

      img {
        width: 60px;
        height: 60px;
        border: none;
        border-radius: 5px;
      }
    }

    &-text {
      width: 100%;
      &-title {
        width: 100%;
        display: flex;
        align-items: center;

        span {
          display: flex;
          align-items: center;
          font-size: 18px;
        }

        &-mark {
          width: 200px;
          margin-left: auto;
          margin-right: -70px;
          margin-top: -10px;
          @media screen and (max-width: 600px) {
            font-size: 20px !important;
          }
          @media screen and (max-width: 430px) {
            margin-top: 0 !important;
          }
        }

        .name {
          font-weight: 700;
          padding-left: 20px;
          padding-right: 10px;
          border-right: 2px dashed #808080;
        }

        .date {
          color: #808080;
          padding-left: 10px;
        }
      }

      &-content {
        padding: 10px 20px 0px 20px;

        span {
          font-size: 18px;
        }
      }
    }

    &-photo {
      padding: 20px 20px 0px 20px;
      display: flex;
      justify-content: space-between;

      img {
        max-height: 300px;
        max-width: 100%;
      }
    }
  }

  &__pagination {
    margin-bottom: 20px;
    display: flex;
    justify-content: center;

    &-item {
      width: 40px;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      transition: border 0.2s ease;

      svg {
        font-size: 30px;
        cursor: pointer;
      }

      span {
        font-size: 30px;
        cursor: pointer;
      }

      &:hover {
        border: 2px solid #ffa500;
        border-radius: 5px;
      }
    }

    .current {
      background-color: #ffa500;
      border: 2px solid #ffa500;
      border-radius: 5px;
      color: #ffffff;
      transition: all 0.2s ease;

      &:hover {
        background-color: #ffffff;
        color: #000000;
      }
    }

    .previous svg {
      padding-right: 2px;
    }

    .next svg {
      padding-left: 2px;
    }
  }
}

@media screen and (max-width: 1060px) {
  .review {
    &__header {
      padding: 0px 20px;
    }
  }
}

@media screen and (max-width: 840px) {
  .review {
    &__header {
      padding: 0px 20px;
    }

    &__form {
      &-text {
        height: 200px;

        textarea {
          font-size: 12px;
        }
      }

      &-controls {
        display: flex;
        flex-wrap: wrap;

        &-buttons {
          width: 100%;

          button {
            width: 100%;
            margin-left: 0px;
            margin-top: 10px;
            font-size: 14px;
          }
        }

        &-file {
          width: 100%;

          label {
            font-size: 14px;
            width: 100%;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 640px) {
  .review {
    &__header {
      span {
        font-size: 14px;
        margin-right: 20px;
      }

      button {
        width: 120px;
        height: auto;
        padding: 10px 20px;
        font-size: 14px;
      }
    }

    &__item {
      &-avatar {
        display: none;
      }

      &-text {
        &-title {
          display: flex;
          flex-wrap: wrap;

          span {
            font-size: 14px;
          }

          &-mark {
            margin: -10px 0px 0px 0px;
            padding-left: 20px;
          }
        }

        &-content {
          padding: 0px 20px;

          span {
            font-size: 12px;
          }
        }
      }

      &-photo {
        padding-top: 10px;
      }
    }
  }
}